.dashboard{
    position: relative;
    display: flex;
    flex-wrap: nowrap;
    width: 100%;
}
.dashboard__c{
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 120px;
    margin-left: 158px;
}
.dashboard__graphic{
    width: 100%;
}
.dashboard__graphic__low{
    display: flex;
    gap: 3%;
}
.dashboard__content{
    display: flex;
    justify-content: space-around;
    gap: 3%;
    padding: 3%;
}
@media screen and (max-width: 1024px) {
    .dashboard__c{
        margin-top: 89px;
    }
}